export default {
  props: ["isSectionTitle"],
  async mounted() {
    const products = await this.$axios.get("/products_random");
    this.latestProducts = products.data;
  },

  components: {
    ProductGridItem: () => import("@/components/product/ProductGridItem"),
    QuickView: () => import("@/components/QuickView"),
  },
  data() {
    return {
      layout: "twoColumn",
      latestProducts: [],
    };
  },
};
